import React, { FC } from 'react';
import { Frame } from 'src/components/Frame';
import * as s from './styles.module.scss';
import { Button } from 'src/components/Button/Button';
import { ShopProductart } from 'src/components/ShopProductart/ShopProductart';
import { gql, useQuery } from '@apollo/client';

const GET_ORDERS = gql`
query Query($getOneId: ID!) {
  orders {
    getOne(id: $getOneId) {
      id
      products {
        _id
        product {
          name
          photo
          desc
          id
          price
        }
      }
    }
  }
}
`;
const Other: FC = () => {
  const orderId = localStorage.getItem('orderId')
  if (!orderId) return (<div className={s.root}>
    <Frame>
      
      <div>
        <Button onClick={() => alert('   ')} label='' />
      </div>
    </Frame>
  </div>)

  const { data, error, loading, refetch } = useQuery(GET_ORDERS, {
    variables: { getOneId: orderId },
  });
  const listProducts = data && data.orders.getOne.products;
  const updateData = () => {
    setTimeout(() => { refetch() }, 100)
  }

  if (loading) return <div>loading...</div>;
  if (error) return <div>{error.message}</div>;
  return (
    <div className={s.root}>
      <Frame>
        
        <div>
          {listProducts.map((order: any, index: any) => {
            return (
              <div>
                <ShopProductart key={order.product.id} id={order.product.id} price={order.product.price} name={order.product.name} description={order.product.desc} image={order.product.photo} refetch={updateData} />
              </div>
            )
          })}
        </div>
        <div>
          <Button onClick={() => updateData()} label='' />
        </div>
      </Frame>
    </div>
  );
};

export default Other;
